import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import useAuth from 'hooks/useAuth'

import SdgSearchModal from './SdgSearchModal'

import { makeStyles } from '@material-ui/styles';

// material-ui
import { styled } from '@material-ui/styles';
import { useTheme } from '@material-ui/core/styles';
import {
    Box,
    Button,
    ButtonBase,
    Divider,
    Drawer,
    Grid,
    IconButton,
    InputAdornment,
    Menu,
    MenuItem,
    Stack,
    TextField,
    Typography,
    useMediaQuery
} from '@material-ui/core';

// third party
import PerfectScrollbar from 'react-perfect-scrollbar';


import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardDoubleArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';


const options = [
    'Agriculture',
    'Non-Profit',
    'Technology',
    'Education',
    'Health'
  ];

  const ITEM_HEIGHT = 48;


// style constant
const useStyles = makeStyles((theme) => ({
    search_goal: {  
        marginBottom: '-10px',
        background: '#F8F8F8', 
        border: '1px Solid grey',
        borderRadius: '10px',

        color: '#696969',
        padding: '5px',
        cursor: 'pointer',
        '&:hover ': {
            background: theme.palette.primary.main,
            borderColor: theme.palette.primary.main,
            color: '#fff'
        },
    },
    root: {
        borderColor: 'green',
        background: 'black',
        '&:hover ': {
            borderColor: 'orange'
        },
}


   
}));

const SearchByBusinessType = ({getBusinessSearchStores}) => {
    const { url } = useAuth()
    const classes = useStyles();

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
      
    };
    const handleClose = () => {
      setAnchorEl(null);
    };


    return (
        <Grid item>
                    <Grid items style={{paddingTop: '5px', paddingBottom: '5px', paddingRight: '5px'}} onClick={handleClick} className={classes.search_goal}>
                        <Stack direction="row" alignItems="center" spacing={1}>
                            <Typography style={{color: '#696969'}} variant="h6">SEARCH BY SECTOR</Typography>
                            <IconButton style={{color: '#fff'}}>
                                <KeyboardArrowDownIcon  style={{fill: "#696969"}} sx={{ width: '1rem', height: '1rem', fontWeight: 800, color: '#fff' }} />
                            </IconButton>
                        </Stack>
                    </Grid>
                    <Menu
                        id="long-menu"
                        MenuListProps={{
                        'aria-labelledby': 'long-button',
                        }}
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        PaperProps={{
                        style: {
                            maxHeight: ITEM_HEIGHT * 4.5,
                            width: '20ch',
                        },
                        }}
                    >
                        {options.map((option) => (
                        <MenuItem key={option} 
                        onClick={()=> {
                            getBusinessSearchStores(option)
                            handleClose()
                            }}>
                            {option}
                        </MenuItem>
                        ))}
                    </Menu>
                    </Grid>
    )
}

export default SearchByBusinessType
