import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useAuth from 'hooks/useAuth'

import SdgSearchModal from './SdgSearchModal'
import SearchByBusinessType from './SearchByBusinessType';

import { makeStyles } from '@material-ui/styles';

// material-ui
import { styled } from '@material-ui/styles';
import { useTheme } from '@material-ui/core/styles';
import {
    Box,
    Button,
    ButtonBase,
    Divider,
    Drawer,
    Grid,
    IconButton,
    InputAdornment,
    Menu,
    MenuItem,
    Stack,
    TextField,
    Typography,
    useMediaQuery
} from '@material-ui/core';

// third party
import PerfectScrollbar from 'react-perfect-scrollbar';

// project imports
import StoreEmpty from './StoreEmpty';
import StoreFilter from './StoreFilter';
import StoreFilterView from './StoreFilterView';
import StoreCard from 'ui-component/cards/StoreCard';
import FloatingCart from 'ui-component/cards/FloatingCart';
import SkeletonProductPlaceholder from 'ui-component/cards/Skeleton/ProductPlaceholder';
import axios from 'utils/axios';
import SortOptions from './SortOptions';
import { SET_MENU, RESET_CART } from 'store/actions';
import { gridSpacing, appDrawerWidth } from 'store/constant';

// assets
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import FilterAltIcon from '@material-ui/icons/FilterAlt';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardDoubleArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ReplayCircleFilled from '@material-ui/icons/ReplayCircleFilled';
import SearchIcon from '@material-ui/icons/Search';
import { RestartAltSharp } from '@material-ui/icons';


const options = [
    'One',
    'Two',
    'Three',
    'Four',
    'Five',
    'Six',
    'Seven',
    'Eight',
    'Nine',
    'Ten',
    'Eleven',
    'Twelve',
    'Thirteen',
    'Fourteen',
    'Fifteen',
    'Sixteen',
    'Seventeen',
  ];
  
  const ITEM_HEIGHT = 48;



/* product list container */
const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'openn' })(({ theme, openn }) => ({
    flexGrow: 1,
    transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen
    }),
    marginRight: -appDrawerWidth,
    [theme.breakpoints.down('lg')]: {
        paddingRight: 0,
        marginRight: 0
    },
    ...(openn && {
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen
        }),
        marginRight: 0
    })
}));



// style constant
const useStyles = makeStyles((theme) => ({
    search_goal: {  
        marginBottom: '-10px',
        background: '#F8F8F8', 
        border: '1px Solid grey',
        borderRadius: '10px',

        color: '#696969',
        padding: '5px',
        cursor: 'pointer',
        '&:hover ': {
            background: theme.palette.primary.main,
            borderColor: theme.palette.primary.main,
            color: '#fff'
        },
    },
    root: {
        borderColor: 'green',
        background: 'black',
        '&:hover ': {
            borderColor: 'orange'
        },
}


   
}));



// ===========================|| E-COMMERCE - PRODUCT GRID ||=========================== //

const Stores = () => {

    const { url } = useAuth()
    const classes = useStyles();

    const dispatch = useDispatch();
    const theme = useTheme();
    const customization = useSelector((state) => state.customization);
    //const cart = useSelector((state) => state.cart);

    const matchDownSM = useMediaQuery(theme.breakpoints.down('sm'));
    const matchDownMD = useMediaQuery(theme.breakpoints.down('md'));
    const matchDownLG = useMediaQuery(theme.breakpoints.down('lg'));






    // sort options
    // const [anchorEl, setAnchorEl] = React.useState(null);
    // const openSort = Boolean(anchorEl);
    // const handleClickListItem = (event) => {
    //     setAnchorEl(event.currentTarget);
    // };

    // const handleClose = () => {
    //     setAnchorEl(null);
    // };

    const [isLoading, setLoading] = React.useState(true);
    React.useEffect(() => {
        setLoading(false);
    }, []);

    // drawer
    const [openn, setOpenn] = React.useState(isLoading);

    const handleDrawerOpen = () => {
        setOpenn((prevState) => !prevState);
    };

    const [searchVal, setSearchVal] = useState('')
    const [stores, setStores] = React.useState([]);

    const [goalSearch, setGoalSearch] = useState(false)
    const [bool, setBool] = useState(false)

    const refresh = () => {
        setBool(!bool)
        setGoalSearch(!goalSearch)
    }

    const getStores = async () => {
        /* const response = await axios.get('http://localhost:8000/api/products');
        setProducts(response.data.products); */


        var requestOptions = {
          method: 'GET',
          headers: {'Content-Type':'application/json'},
          redirect: 'follow'
        };

        /* https://goodshop-backend-2.herokuapp.com/users/profiles */

        if (searchVal === "") {
            const res = await fetch(`${url}/api/real-stores`, requestOptions)

            console.log('RES')
            console.log(res)
            const data = await res.json()

            console.log('DATA')
            console.log(data)

            for (var i = 0; i < data.length; i++) {
                console.log("LOGO", data[i].store_logo)
                let tempLogo = data[i].store_logo
                if (tempLogo !== null) {
                    let tempArr = Array.from(tempLogo);
                    if (tempArr[0] !== 'h') {
                        let tempUrl = url
                        console.log("Temporary URL",tempUrl)
                        tempUrl += data[i].store_logo
                        console.log("Temporary URL 2",tempUrl)
                        data[i].store_logo = tempUrl
                    }
                }   
            }

            setStores(data)
        } else {
            const res = await fetch(`${url}/api/search-store/?search=${searchVal}`, requestOptions)

            console.log('RES')
            console.log(res)
            const data = await res.json()

            console.log('DATA')
            console.log(data)

            for (var i = 0; i < data.length; i++) {
                console.log("LOGO", data[i].store_logo)
                let tempLogo = data[i].store_logo
                if (tempLogo !== null) {
                    let tempArr = Array.from(tempLogo);
                    if (tempArr[0] !== 'h') {
                        url += data[i].store_logo
                        data[i].store_logo = url
                    }
                }   
            }

            setStores(data)
        }
        

        
    };

    // const [open, setOpen] = React.useState(false);
    //     const handleClickOpenDialog = () => {
    //         console.log('Dialog Opened')
    //         setOpen(true);
    //         setGoalSearch(true)
    //     };
    //     const handleCloseDialog = (id) => {
    //         getSdgSearchStores(id)
    //         setOpen(false);
    //     };

    //     const closeModal = () => {
    //         setOpen(false);
    //     };

    const getSdgSearchStores = async (option) => {
        var requestOptions = {
          method: 'GET',
          headers: {'Content-Type':'application/json'},
          redirect: 'follow'
        };
        const res = await fetch(`${url}/api/search-sdg-store/?search=${option}`, requestOptions)

            console.log('RES')
            console.log(res)
            const data = await res.json()

            console.log('DATA')
            console.log(data)

            for (var i = 0; i < data.length; i++) {
                console.log("LOGO", data[i].store_logo)
                let tempLogo = data[i].store_logo
                if (tempLogo !== null) {
                    let tempArr = Array.from(tempLogo);
                    if (tempArr[0] !== 'h') {
                        url += data[i].store_logo
                        data[i].store_logo = url
                    }
                }   
            }

            setStores(data)
    }

    const getBusinessSearchStores = async(option) => {
        var requestOptions = {
            method: 'GET',
            headers: {'Content-Type':'application/json'},
            redirect: 'follow'
          };
          const res = await fetch(`${url}/api/search-business-type/?search=${option}`, requestOptions)
  
              console.log('RES')
              console.log(res)
              const data = await res.json()
  
              console.log('DATA')
              console.log(data)
  
              for (var i = 0; i < data.length; i++) {
                  console.log("LOGO", data[i].store_logo)
                  let tempLogo = data[i].store_logo
                  if (tempLogo !== null) {
                      let tempArr = Array.from(tempLogo);
                      if (tempArr[0] !== 'h') {
                          url += data[i].store_logo
                          data[i].store_logo = url
                      }
                  }   
              }
  
              setStores(data)
      }
    
    


    React.useEffect(() => {
        getStores();

        // hide left drawer when email app opens
        dispatch({ type: SET_MENU, opened: false });

        // clear cart if complete order
        /* if (cart.checkout.step > 2) {
            dispatch({ type: RESET_CART });
        } */

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchVal, bool]);

    // filter
    const initialState = {
        search: '',
        sort: 'low',
        gender: [],
        categories: ['all'],
        colors: [],
        price: '',
        rating: 0
    };

    const [filter, setFilter] = React.useState(initialState);

    const filterIsEqual = (a1, a2) =>
        a1 === a2 ||
        (a1.length === a2.length &&
            a1.search === a2.search &&
            a1.sort === a2.sort &&
            a1.price === a2.price &&
            a1.rating === a2.rating &&
            JSON.stringify(a1.gender) === JSON.stringify(a2.gender) &&
            JSON.stringify(a1.categories) === JSON.stringify(a2.categories) &&
            JSON.stringify(a1.colors) === JSON.stringify(a2.colors));

    const handelFilter = (type, params) => {
        setLoading(true);
        switch (type) {
            case 'gender':
                if (filter.gender.some((item) => item === params)) {
                    setFilter({ ...filter, gender: filter.gender.filter((item) => item !== params) });
                } else {
                    setFilter({ ...filter, gender: [...filter.gender, params] });
                }
                break;
            case 'categories':
                if (filter.categories.some((item) => item === params)) {
                    setFilter({ ...filter, categories: filter.categories.filter((item) => item !== params) });
                } else {
                    setFilter({ ...filter, categories: [...filter.categories, params] });
                    if (filter.categories.some((item) => item === 'all') || params === 'all') {
                        setFilter({ ...filter, categories: [params] });
                    }
                }

                break;
            case 'colors':
                if (filter.colors.some((item) => item === params)) {
                    setFilter({ ...filter, colors: filter.colors.filter((item) => item !== params) });
                } else {
                    setFilter({ ...filter, colors: [...filter.colors, params] });
                }
                break;
            case 'price':
                setFilter({ ...filter, price: params });
                break;
            case 'search':
                setFilter({ ...filter, search: params });
                break;
            case 'sort':
                setFilter({ ...filter, sort: params });
                break;
            case 'rating':
                setFilter({ ...filter, rating: params });
                break;
            case 'reset':
                setFilter(initialState);
                break;
            default:
            // no options
        }
    };

    const filterData = async () => {
        /* await axios
            .post('/api/products/filter', {
                filter
            })
            .then((res) => {
                setProducts(res.data.results);
                setLoading(false);
            });*/
    }; 

    React.useEffect(() => {
        filterData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [filter]);

    React.useEffect(() => {
        setOpenn(!matchDownLG);
    }, [matchDownLG]);

    // sort filter
    // const handleMenuItemClick = (event, index) => {
    //     setFilter({ ...filter, sort: index });
    //     setAnchorEl(null);
    // };

    const sortLabel = SortOptions.filter((items) => items.value === filter.sort);

    // search filter
    const handleSearch = async (event) => {
        /* const newString = event.target.value;
        setFilter({ ...filter, search: newString }); */
        setSearchVal(event.target.value)
        console.log(searchVal)
    };

    let storeResult = '';
    if (stores) {

        //Filtering Approved Stores Only
        storeResult = stores.filter(store => store.is_approved === true)
            .map((store, index) => (
            <Grid key={index} item xs={12} sm={6} md={4} lg={3}>
                <StoreCard
                    id={store.id}
                    store_logo={store.store_logo}
                    name={store.name}
                    desc={store.desc}
                    price={store.price}
                    salePrice={store.salePrice}
                    rating={store.rating}
                    sdg_goals={store.sdg_goals}
                    /* color={store.colors[0]} */
                />
            </Grid>
        ));
    }

    const spacingMD = matchDownMD ? 1 : 1.5;



    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
      
    };
    const handleClose = () => {
      setAnchorEl(null);
    };

    



    return (
        <Grid container spacing={2} sx={{marginTop:{xs: '-105px', sm: '-180px', md: '-120px'}, marginBottom: '25px'}}>
            <Grid item xs={12}>
                <Grid container alignItems="center" justifyContent="space-between" spacing={matchDownMD ? 0.5 : 2}>
                    <Grid style={{marginBottom: '-65px', marginLeft: '0px'}}>
                    <Stack direction="row" spacing={1}>
                    <Grid >
                    <Stack direction="row" spacing={1}>
                    <Grid item>
                    <Grid items style={{paddingTop: '5px', paddingBottom: '5px', paddingRight: '5px'}} onClick={handleClick} className={classes.search_goal}>
                        <Stack direction="row" alignItems="center" spacing={1}>
                            <Typography style={{color: '#696969'}} variant="h6">SEARCH BY GOAL</Typography>
                            <IconButton style={{color: '#fff'}}>
                                <KeyboardArrowDownIcon  style={{fill: "#696969"}} sx={{ width: '1rem', height: '1rem', fontWeight: 800, color: '#fff' }} />
                            </IconButton>
                        </Stack>
                    </Grid>
                    <Menu
                        id="long-menu"
                        MenuListProps={{
                        'aria-labelledby': 'long-button',
                        }}
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        PaperProps={{
                        style: {
                            maxHeight: ITEM_HEIGHT * 4.5,
                            width: '20ch',
                        },
                        }}
                    >
                        {options.map((option) => (
                        <MenuItem key={option} 
                        onClick={()=> {
                            getSdgSearchStores(option)
                            handleClose()
                            }}>
                            {option}
                        </MenuItem>
                        ))}
                    </Menu>
                     
                    </Grid>
                    <Grid item>
                        <SearchByBusinessType getBusinessSearchStores={getBusinessSearchStores}/>
                    </Grid>
                    </Stack>
                    </Grid>
                    <Grid sx={{ display: { xs: 'none', md:'block', lg:'block', sm: 'none' } }}>
                    <IconButton  sx={{display:{xs: 'none', sm: 'none', md: 'block', lg: 'block'}}} onClick={refresh} style={{color: '#3f7e44'}} sx={{marginBottom: '-15px'}}>
                            <RestartAltSharp style={{fill: "#3f7e44"}} sx={{ width: '35px', height: '35px', fontWeight: 500, color: 'grey.500' }} />
                    </IconButton>
                    </Grid>
                    </Stack>
                    </Grid>
                   
                   
                    <Grid item sx={{width:{xs: '100%' , md: '280px'}}}>
                        {/* <Stack direction="row" alignItems="center" justifyContent="center" spacing={matchDownSM ? 0.5 : spacingMD}> */}
                            <TextField
                                /* style={{borderColor: 'green'}} */
                                sx={{marginTop:{xs: '65px'}, width:{xs: '100%', md: '280px'}, marginLeft:{xs: '0px'}}}
                                // sx={{ width: { xs: 140, md: 'auto' }, borderColor: 'green',  }}
                                /* InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon fontSize="small" />
                                        </InputAdornment>
                                    )
                                }} */
                                /* value={filter.search} */  
                                placeholder="Search Store Name"
                                size="small"
                                onChange={handleSearch}

                                 InputProps={{
                                  classes: {
                                     root: classes.root
                                  }
                               }}
                            />
                            {/* <Typography sx={{ pl: 1.5, fontSize: '1rem', color: 'grey.500', fontWeight: 400 }}>|</Typography>

                            <ButtonBase disableRipple onClick={handleDrawerOpen}>
                                <FilterAltIcon sx={{ fontWeight: 500, color: 'secondary.200' }} />
                                <Typography variant="h5" sx={{ mt: 0.5, display: { xs: 'none', sm: 'block' } }}>
                                    Filter
                                </Typography>
                            </ButtonBase>

                            <Typography sx={{ display: { xs: 'none', sm: 'flex' }, fontSize: '1rem', color: 'grey.500', fontWeight: 400 }}>
                                |
                            </Typography> */}
                            {/* <Stack direction="row" alignItems="center" justifyContent="center" sx={{ display: { xs: 'none', sm: 'flex' } }}>
                                <Typography variant="h5">Sort by: </Typography>
                                <Button
                                    id="demo-positioned-button"
                                    aria-controls="demo-positioned-menu"
                                    aria-haspopup="true"
                                    aria-expanded={openSort ? 'true' : undefined}
                                    onClick={handleClickListItem}
                                    sx={{ color: 'grey.500', fontWeight: 400 }}
                                    endIcon={<KeyboardArrowDownIcon />}
                                >
                                    {sortLabel.length > 0 && sortLabel[0].label}
                                </Button>
                                <Menu
                                    id="demo-positioned-menu"
                                    aria-labelledby="demo-positioned-button"
                                    anchorEl={anchorEl}
                                    open={openSort}
                                    onClose={handleClose}
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'right'
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right'
                                    }}
                                >
                                    {SortOptions.map((option, index) => (
                                        <MenuItem
                                            sx={{ p: 1.5 }}
                                            key={index}
                                            selected={option.value === filter.sort}
                                            onClick={(event) => handleMenuItemClick(event, option.value)}
                                        >
                                            {option.label}
                                        </MenuItem>
                                    ))}
                                </Menu>
                            </Stack> */}
                        {/* </Stack> */}
                    </Grid>
                
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <Divider sx={{ borderColor: 'grey.400' }} />
            </Grid>
            <Grid item xs={12}>
                <Box sx={{ display: 'flex' }}>
                    <Main openn={openn}>
                        {/* <ProductFilterView
                            filter={filter}
                            filterIsEqual={filterIsEqual}
                            handelFilter={handelFilter}
                            initialState={initialState}
                        /> */}
                        <Grid container spacing={gridSpacing}>
                            {isLoading
                                ? [1, 2, 3, 4, 5, 6, 7, 8].map((item) => (
                                      <Grid key={item} item xs={12} sm={6} md={4} lg={3}>
                                          <SkeletonProductPlaceholder />
                                      </Grid>
                                  ))
                                : storeResult}

                            {stores.length <= 0 && (
                                <Grid item xs={12} sx={{ mt: 3 }}>
                                    <StoreEmpty />
                                </Grid>
                            )}
                        </Grid>
                    </Main>
                    {/* <Drawer
                        sx={{
                            ml: open ? 3 : 0,
                            height: matchDownLG ? '100vh' : 'auto',
                            flexShrink: 0,
                            zIndex: { xs: 1200, lg: open ? 0 : -1 },
                            overflowX: 'hidden',
                            width: appDrawerWidth,
                            '& .MuiDrawer-paper': {
                                height: 'auto',
                                width: appDrawerWidth,
                                position: matchDownLG ? 'fixed' : 'relative',
                                border: 'none',
                                borderRadius: matchDownLG ? 0 : `${customization.borderRadius}px`
                            }
                        }}
                        variant={matchDownLG ? 'temporary' : 'persistent'}
                        anchor="right"
                        open={open}
                        ModalProps={{ keepMounted: true }}
                        onClose={handleDrawerOpen}
                    >
                        <PerfectScrollbar component="div">
                            <StoreFilterFilter filter={filter} handelFilter={handelFilter} />
                        </PerfectScrollbar>
                    </Drawer> */}
                </Box>
            </Grid>
            {/* <FloatingCart /> */}
             {/* <SdgSearchModal open={open} closeModal={closeModal} handleCloseDialog={handleCloseDialog}/> */}
        </Grid>
    );
};

export default Stores;
