import PropTypes from 'prop-types';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

// material-ui
import { Button, CardContent, CardMedia, Grid, Rating, Stack, Typography } from '@material-ui/core';

// project import
import MainCard from './MainCard';
import SkeletonProductPlaceholder from 'ui-component/cards/Skeleton/ProductPlaceholder';
import { ADD_PRODUCTS, SNACKBAR_OPEN } from 'store/actions';
import useAuth from 'hooks/useAuth';

// assets
import ShoppingCartTwoToneIcon from '@material-ui/icons/ShoppingCartTwoTone';

/* const prodImage = require.context('assets/images/e-commerce', true); */

// ===========================|| PRODUCT CARD ||=========================== //

const ProductCard = ({ id, owner, name, product_image, desc, price, rating }) => {
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const { url } = useAuth()

    /* const prodProfile = product_image && prodImage(`./${product_image}`).default; */
    const [productRating] = React.useState(rating);

    const myImage = `${product_image}`

    const addCart = () => {
        dispatch({
            type: ADD_PRODUCTS,
            product: { id, owner, name, product_image, price, quantity: 1 }
        });
        dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Add To Cart Success',
            variant: 'alert',
            alertSeverity: 'success'
        });
    };

    const [isLoading, setLoading] = React.useState(true);
    React.useEffect(() => {
        setLoading(false);
    }, []);

    const goToDetails = () => {
        navigate(`/e-commerce/productdetails/${id}`)
    }

    return (
        <>
            {isLoading ? (
                <SkeletonProductPlaceholder />
            ) : (
                <MainCard
                    content={false}
                    boxShadow
                    sx={{
                        '&:hover': {
                            transform: 'scale3d(1.02, 1.02, 1)',
                            transition: 'all .4s ease-in-out'
                        }
                    }}
                >
                    <CardMedia
                        sx={{ height: 220 }}
                        image={myImage}
                        title="Contemplative Reptile"
                        onClick={goToDetails}
                        // component={Link}
                        // to={`/e-commerce/product-details/${id}`}
                    />
                    <CardContent sx={{ p: 2 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Typography
                                    /* component={Link}
                                    to={`/e-commerce/product-details/${id}`} */
                                    variant="subtitle1"
                                    sx={{ textDecoration: 'none',
                                    overflow: 'hidden',
                                    height: 30 }}
                                >
                                    {name}
                                </Typography>
                            </Grid>
                            {desc && (
                                <Grid item xs={12}>
                                    <Typography
                                        variant="body2"
                                        sx={{
                                            overflow: 'hidden',
                                            height: 45
                                        }}
                                    >
                                        {desc}
                                    </Typography>
                                </Grid>
                            )}
                            {/* <Grid item xs={12} sx={{ pt: '8px !important' }}>
                                <Stack direction="row" alignItems="center" spacing={1}>
                                    <Rating precision={0.5} name="size-small" defaultValue={rating} size="small" readOnly />
                                    <Typography variant="caption">({price}+)</Typography>
                                </Stack>
                            </Grid> */}
                            <Grid item xs={12}>
                                <Stack direction="row" justifyContent="space-between" alignItems="center">
                                    <Grid container spacing={1}>
                                        <Grid item>
                                            <Typography variant="h4">${price}</Typography>
                                        </Grid>
                                        {/* <Grid item>
                                            <Typography variant="h6" sx={{ color: 'grey.500', textDecoration: 'line-through' }}>
                                                ${salePrice}
                                            </Typography>
                                        </Grid> */}
                                    </Grid>
                                    <Button variant="contained" sx={{ minWidth: 0 }} onClick={addCart}>
                                        <ShoppingCartTwoToneIcon fontSize="small" />
                                    </Button>
                                </Stack>
                            </Grid>
                        </Grid>
                    </CardContent>
                </MainCard>
            )}
        </>
    );
};

ProductCard.propTypes = {
    id: PropTypes.number,
    /* color: PropTypes.string, */
    name: PropTypes.string,
    product_image: PropTypes.string,
    desc: PropTypes.string,
    price: PropTypes.number,
    /* salePrice: PropTypes.number, */
    rating: PropTypes.number
};

export default ProductCard;
