import PropTypes from 'prop-types';
import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';

// material-ui
import { Button, Dialog, Divider, Grid, Stack, Typography, Zoom, useMediaQuery } from '@material-ui/core';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { gridSpacing } from 'store/constant';
import useAuth from 'hooks/useAuth'

// third-party
// third-party
import { Chance } from 'chance';

// assets
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import completed from 'assets/images/e-commerce/discount.png';

const chance = new Chance();

const Transition = React.forwardRef((props, ref) => <Zoom ref={ref} {...props} />);

// ===========================|| CHECKOUT CART - DISCOUNT COUPON CODE ||=========================== //

const ServiceRequestModal = ({ open, handleCloseDialog }) => {
    const matchDownMD = useMediaQuery((theme) => theme.breakpoints.down('sm'));
    const { productUserFilterValue } = useAuth(); 
    const [profile, setProfile] = useState([])

    useEffect( async ()=> {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`http://localhost:8000/users/profile/${productUserFilterValue}`, requestOptions)
        const data = await res.json()
        setProfile(data)
    }, [])


    return (
        <Dialog
            open={open}
            onBlur={handleCloseDialog}
            onBackdropClick={handleCloseDialog}
            TransitionComponent={Transition}
            keepMounted
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
            maxWidth="md"
            sx={{
                '& .MuiDialog-paper': {
                    p: 0
                }
            }}
        >
            <MainCard>
                <Grid container direction="column" spacing={gridSpacing} alignItems="center" justifyContent="center" sx={{ my: 3 }}>
                    <Grid item xs={12}>
                        <Typography variant={matchDownMD ? 'h2' : 'h1'}>Thank you for Request!</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack alignItems="center" spacing={2}>
                            <Typography align="center" variant="h4" sx={{ fontWeight: 400, color: 'grey.500' }}>
                                Contact {' '}
                                <Typography variant="subtitle1" component="span" color="primary">
                                    {profile.full_name}
                                </Typography>
                                 {' '}to Finalize Your Service Request
                            </Typography>
                            <Typography variant="body1" align="center">
                                Phone: {' '}
                                <Typography variant="subtitle1" component="span" color="primary">
                                    {profile.phone}
                                </Typography>
                            </Typography>
                            <Typography variant="body1" align="center">
                                Email: {' '}
                                <Typography variant="subtitle1" component="span" color="primary">
                                    {profile.email}
                                </Typography>
                            </Typography>
                        </Stack>
                    </Grid>
                    <Grid item xs={12} sx={{ m: 3 }}>
                        <img src={completed} alt="Order Complete" width="100%" style={{ maxWidth: 780 }} />
                    </Grid>
                    <Grid item xs={12} sm={9}>
                        <Stack alignItems="center" spacing={1}>
                            <Typography variant="caption" align="center">
                                If you have any query or questions regarding service, then fell to get in contact us
                            </Typography>
                            <Typography variant="subtitle1" color="error" sx={{ cursor: 'pointer' }}>
                                {chance.phone()}
                            </Typography>
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Divider />
                    </Grid>
                    {/* <Grid item xs={12}>
                        <Grid
                            direction={matchDownMD ? 'column-reverse' : 'row'}
                            container
                            spacing={3}
                            alignItems={matchDownMD ? '' : 'center'}
                            justifyContent="space-between"
                        >
                            <Grid item>
                                <Button component={Link} to="/e-commerce/products" variant="string" startIcon={<KeyboardBackspaceIcon />}>
                                    Continue Shopping
                                </Button>
                            </Grid>
                            <Grid item>
                                <Button component={Link} to="/e-commerce/products" variant="contained" fullWidth>
                                    Download Invoice
                                </Button>
                            </Grid>
                        </Grid>
                    </Grid> */}
                </Grid>
            </MainCard>
        </Dialog>
    );
};

ServiceRequestModal.propTypes = {
    open: PropTypes.bool
};

export default ServiceRequestModal;
