import React from 'react';

// material-ui
import { makeStyles } from '@material-ui/styles';

// project imports
import Header from '../landing/Header';
import Footer from '../landing/Footer';
import StoreShowcase from './StoreShowcase';

// project imports additions
import Products from '../../application/e-commerce/Products'
import Services from '../../application/e-commerce/Services'
import Stores from '../../application/e-commerce/Stores'


import Customization from 'layout/Customization';
import AppBar from 'ui-component/extended/AppBar';



// style constant
const useStyles = makeStyles((theme) => ({
    header: {
        paddingTop: '30px',
        overflowX: 'hidden',
        overflowY: 'clip',
        [theme.breakpoints.down('sm')]: {
            paddingTop: '0px'
        }
    },
    sectionWhite: {
        paddingTop: '75px',   
        [theme.breakpoints.down('sm')]: {
            paddingTop: '35px'
        }
    },
    sectionShowcase: {
        paddingTop: '40px',   
        [theme.breakpoints.down('sm')]: {
            paddingTop: '25px'
        }
    }
}));

// =============================||  MAIN ||============================= //


const Landing = () => {
    const classes = useStyles();

    
    return (
        <>
            <div id="home" className={classes.header}>
                <AppBar />
                {/* <Header /> */}
            </div>
            <div className={classes.sectionShowcase} style={{paddingLeft: '20px', paddingRight: '20px'}}>
                <StoreShowcase />
            </div>
            <div className={classes.sectionWhite} style={{paddingLeft: '60px', paddingRight: '60px'}}>
                <Products />
            </div>
            <div className={classes.sectionWhite} style={{paddingLeft: '60px', paddingRight: '60px', paddingBottom: '30px'}}>
                <Services />
            </div>

            <Footer />
            {/* <Customization /> */}
        </>
    );
};

export default Landing;
