import React,{ useEffect, useState } from 'react'

/* import Paper from '@material-ui/Paper' */

import { makeStyles, useTheme } from '@material-ui/styles';
import { Box, Button, Container, Grid, Link, Typography } from '@material-ui/core';
import useAuth from 'hooks/useAuth';

// third party
import { motion } from 'framer-motion';
import { gridSpacing } from 'store/constant';

 import banner from 'assets/images/banner-01.jpg';

const styles = {
	paperContainer: {
		
	}
}

const StoreShowcase = () => {
    const theme = useTheme();
    const { productUserFilterValue, url } = useAuth();

    const [store, setStore] = useState([])

    useEffect( async() => {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };                               
        const res = await fetch(`${url}/api/single-store/${productUserFilterValue}`, requestOptions)
        const data = await res.json()
        setStore(data)
        console.log("Store Data", data)
    }, [])

        
	return (
		

							<motion.div
                                initial={{ opacity: 0, translateY: 550 }}
                                animate={{ opacity: 1, translateY: 0 }}
                                transition={{
                                    type: 'spring',
                                    stiffness: 150,
                                    damping: 30
                                }}
                            >
                                {/* <Typography
                                    variant="h1"
                                    sx={{
                                        fontSize: { xs: '36px', sm: '48px', md: '64px' },
                                        fontWeight: '900',
                                        lineHeight: { xs: '42px', sm: '56px', md: '80px' }
                                    }}
                                >
                                    Welcome This Is 
                                    <Box component="span" sx={{ ml: 2, color: theme.palette.primary.main }}>
                                        Accelerate 2030
                                    </Box>
                                </Typography> */}
                                <div style={{backgroundImage: `url(${banner})`,
                                                backgroundSize: 'cover',
                                                backgroundRepeat: 'co-repeat',
                                                backgroundPosition: 'center',
                                                height: '60vh',
                                                }}>
                                    <Grid

                                        container
                                        alignItems="center"
                                        justifyContent="space-around"
                                        spacing={gridSpacing}
                                        sx={{ mt: '25px', mb: '80px', [theme.breakpoints.down('sm')]: { mt: { xs: '35px', sm: '32px' }, mb: '20px' } }}

                                        >
                                    <Grid item xs={12}>
									<Typography
                                    variant="h1"
                                    sx={{
                                        fontSize: { xs: '20px', sm: '48px', md: '64px' },
                                        fontWeight: { xs: '250', sm: '400' },
                                        lineHeight: { xs: '32px', sm: '56px', md: '80px' },
                                        textAlign: 'center', 
                                        marginTop:{xs: '38px', sm: '60px'}
                                    }}
                                >
                                    
                                    <Box component="span" sx={{ ml: 2, color: '#fff' }} style={{fontSize: '52px'}}>
                                       {store.name}
                                    </Box>
                                </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                <Typography
                                    variant="h4"
                                    component="div"
                                    /* color="inherit" */
                                    sx={{
                                        fontSize: { xs: '1rem',  md: '1.125rem' },
                                        fontWeight: '400',
                                        lineHeight: { xs: '24px', md: '32px' },
                                        textAlign: 'center',
                                        marginLeft: { xs: '10%', md: '20%' },
                                        marginRight: { xs: '10%', md: '20%' },
                                        
                                        color: '#fff'
                                    }}
                                >
                                    {store.desc}
                                </Typography>
                                </Grid>
                                </Grid>
								</div>
                            </motion.div>
	)
}

export default StoreShowcase