import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';

// material-ui
import { Button, CardContent, CardMedia, Grid, Rating, Stack, Typography } from '@material-ui/core';

// project import
import MainCard from './MainCard';
import SkeletonProductPlaceholder from 'ui-component/cards/Skeleton/ProductPlaceholder';
import { ADD_PRODUCTS, SNACKBAR_OPEN } from 'store/actions';
import ServiceRequestModal from './ServiceRequestModal'
import useAuth from 'hooks/useAuth';

// assets
import ShoppingCartTwoToneIcon from '@material-ui/icons/DoneAllTwoTone';

/* const prodImage = 'http://localhost:8000' */

// ===========================|| PRODUCT CARD ||=========================== //

const ServiceCard = ({ id, name, service_image, desc, price, rating, discount, availability }) => {
    const dispatch = useDispatch();
    const { url } = useAuth()

    /* const prodProfile = service_image && prodImage(`${service_image}`).default; */
    const [serviceRating] = React.useState(rating);

    const myImage = `${service_image}`

    /* const addCart = () => {
        dispatch({
            type: ADD_PRODUCTS,
            product: { id, name, image, salePrice, offerPrice, color, size: 8, quantity: 1 }
        });
        dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Add To Cart Success',
            variant: 'alert',
            alertSeverity: 'success'
        });
    }; */


    const [open, setOpen] = React.useState(false);
        const handleClickOpenDialog = () => {
            setOpen(true);
        };
        const handleCloseDialog = () => {
            setOpen(false);
        };

    const [isLoading, setLoading] = React.useState(true);
    React.useEffect(() => {
        setLoading(false);
        console.log("PRICE", price)
    }, []);

    return (
        <>
            {isLoading ? (
                <SkeletonProductPlaceholder />
            ) : (
                <MainCard
                    content={false}
                    boxShadow
                    sx={{
                        '&:hover': {
                            transform: 'scale3d(1.02, 1.02, 1)',
                            transition: 'all .4s ease-in-out'
                        }
                    }}
                >
                    <CardMedia
                        sx={{ height: 220 }}
                        image={myImage}
                        title="Contemplative Reptile"
                        /* component={Link}
                        to={`/e-commerce/product-details/${id}`} */
                    />
                    <CardContent sx={{ p: 2 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Typography
                                    /* component={Link}
                                    to={`/e-commerce/product-details/${id}`} */
                                    variant="subtitle1"
                                    sx={{ textDecoration: 'none' }}
                                >
                                    {name}
                                </Typography>
                            </Grid>
                            {desc && (
                                <Grid item xs={12}>
                                    <Typography
                                        variant="body2"
                                        sx={{
                                            overflow: 'hidden',
                                            height: 45
                                        }}
                                    >
                                        {desc}
                                    </Typography>
                                </Grid>
                            )}
                            {/* <Grid item xs={12} sx={{ pt: '8px !important' }}>
                                <Stack direction="row" alignItems="center" spacing={1}>
                                    <Rating precision={0.5} name="size-small" defaultValue={serviceRating} size="small" readOnly />
                                    <Typography variant="caption">({price}+)</Typography>
                                </Stack>
                            </Grid> */}
                            <Grid item xs={12}>
                                <Stack direction="row" justifyContent="space-between" alignItems="center">
                                    <Grid container spacing={1}>
                                        <Grid item>
                                            <Typography variant="h4">${price}</Typography>
                                        </Grid>
                                        {/* <Grid item>
                                            <Typography variant="h6" sx={{ color: 'grey.500', textDecoration: 'line-through' }}>
                                                ${salePrice}
                                            </Typography>
                                        </Grid> */}
                                    </Grid>
                                    <Button variant="contained" sx={{ minWidth: 0 }} onClick={handleClickOpenDialog}>
                                        <ShoppingCartTwoToneIcon fontSize="small" />
                                    </Button>
                                </Stack>
                            </Grid>
                        </Grid>
                    </CardContent>
                    <ServiceRequestModal open={open} handleCloseDialog={handleCloseDialog}/>
                </MainCard>
            )}
        </>
    );
};

ServiceCard.propTypes = {
    id: PropTypes.number,
    /* color: PropTypes.string, */
    name: PropTypes.string,
    service_image: PropTypes.string,
    desc: PropTypes.string,
    price: PropTypes.number,
    /* salePrice: PropTypes.number, */
    rating: PropTypes.number
};

export default ServiceCard;
