import React, {useState} from 'react';
import { sum } from 'lodash';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

//Additions
import CustomerLoginForm from './CustomerLoginForm'
import CustomerRegisterForm from './CustomerRegisterForm'
import useAuth from 'hooks/useAuth';
import { ADD_PRODUCTS, SNACKBAR_OPEN } from 'store/actions';

// material-ui
import { useTheme, styled } from '@material-ui/styles';
import { Fab, Badge, IconButton } from '@material-ui/core';

// assets
import ShoppingCartTwoToneIcon from '@material-ui/icons/ShoppingCartTwoTone';

const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        right: 0,
        top: 3,
        border: `2px solid ${theme.palette.background.paper}`,
        padding: '0 4px'
    }
}));

// ===========================|| CART ITEMS - FLOATING BUTTON ||=========================== //

const FloatingCart = () => {

    const dispatch = useDispatch()
    const { isCustomerLoggedIn } = useAuth();
    const [open, setOpen] = React.useState(false);
    const navigate = useNavigate();

        const handleClickOpenDialog = () => {
            setOpen(true);
        };
        const handleCloseDialog = () => {
            console.log("CLOSED")
            setOpen(false);
        };
        /* const handleRegClickOpenDialog = () => {

            setRegOpen(true);
            setOpen(false)
        };
        const handleRegCloseDialog = () => {
            console.log("CLOSED")
            setRegOpen(false);
        }; */


    const theme = useTheme();
    const cart = useSelector((state) => state.cart);
    const totalQuantity = sum(cart.checkout.products.map((item) => item.quantity));

    const openCart = () => {
        navigate('/heckout', { replace: true });
    }

    const loginPrompt = () => {
        dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Please Login',
            variant: 'alert',
            alertSeverity: 'success'
        });
    }

    return (
        <>
        <Fab
            /* component={Link}
            to="/heckout" */
            size="large"
            variant="string"
            sx={{
                top: '25%',
                position: 'fixed',
                right: 0,
                zIndex: (theme) => theme.zIndex.speedDial,
                boxShadow: theme.customShadows.warning,
                bgcolor: 'warning.main',
                color: 'warning.light',
                borderRadius: '8px',
                borderTopRightRadius: 0,
                borderBottomRightRadius: 0,
                '&:hover': {
                    bgcolor: 'warning.dark',
                    color: 'warning.light'
                }
            }}
            /* onClick={cartIconClicked} */
            onClick= {isCustomerLoggedIn ? openCart : loginPrompt}
        >
            <IconButton disableRipple aria-label="cart" sx={{ '&:hover': { bgcolor: 'transparent' } }} size="large">
                <StyledBadge showZero badgeContent={totalQuantity} color="error">
                    <ShoppingCartTwoToneIcon sx={{ color: 'grey.800' }} />
                </StyledBadge>
            </IconButton>
        </Fab>
        <CustomerLoginForm open={open} handleCloseDialog={handleCloseDialog}/>

        {/* <CustomerRegisterForm open={open} handleRegCloseDialog={handleRegCloseDialog}
                                open={open} handleCloseDialog={handleCloseDialog}
                                handleRegClickOpenDialog={handleRegClickOpenDialog}/> */}
        </>
    );
};

export default FloatingCart;
