import React from 'react';

// material-ui
import { makeStyles } from '@material-ui/styles';
import { CardMedia, Grid, Typography } from '@material-ui/core';

// project imports
import { gridSpacing } from 'store/constant';

// assets
import imageEmpty from 'assets/images/e-commerce/empty.png';

// style constant
const useStyles = makeStyles({
    emailEmptyContent: {
        maxWidth: '500px',
        margin: '0 auto',
        textAlign: 'center'
    }
});

// ===========================|| NO/EMPTY Product ||=========================== //

const ProductEmpty = () => {
    const classes = useStyles();

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <div className={classes.emailEmptyContent}>
                    <Grid container justifyContent="center" spacing={gridSpacing}>
                        <Grid item xs={12}>
                            <CardMedia component="img" image={imageEmpty} title="Slider5 image" />
                        </Grid>
                        <Grid item xs={12}>
                            <Grid container spacing={gridSpacing}>
                                <Grid item xs={12}>
                                    <Typography variant="h1" color="inherit" component="div">
                                        No Services Available In This Store
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography variant="body2">Try checking your spelling or use more general terms</Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            </Grid>
        </Grid>
    );
};

export default ProductEmpty;
